import React, { useEffect, useState } from "react";
import { ref, onValue } from "firebase/database";
import { db } from "../firebaseConfig";

export default function AdminInquiries() {
  const [inquiries, setInquiries] = useState([]);
  const [loggedIn, setLoggedIn] = useState(false);
  const [password, setPassword] = useState("");

  //  Change your admin password here
  const ADMIN_PASSWORD = "itlab@123";

  // Fetch inquiries once logged in
  useEffect(() => {
    if (loggedIn) {
      const inquiryRef = ref(db, "inquiries");
      onValue(inquiryRef, (snapshot) => {
        const data = snapshot.val();
        if (data) {
          const formatted = Object.entries(data).map(([id, value]) => ({
            id,
            ...value,
          }));
          setInquiries(formatted.reverse());
        } else {
          setInquiries([]);
        }
      });
    }
  }, [loggedIn]);

  // Password check
  const handleLogin = (e) => {
    e.preventDefault();
    if (password === ADMIN_PASSWORD) {
      setLoggedIn(true);
    } else {
      alert("Incorrect password!");
    }
  };

  // If not logged in  show password form
  if (!loggedIn) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-blue-50 to-white">
        <div className="bg-white p-8 shadow-lg rounded-2xl w-80 text-center">
          <h2 className="text-2xl font-semibold text-blue-700 mb-4">
            Admin Login
          </h2>
          <form onSubmit={handleLogin}>
            <input
              type="password"
              placeholder="Enter admin password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full p-2 border rounded-lg mb-4"
              required
            />
            <button
              type="submit"
              className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 w-full"
            >
              Login
            </button>
          </form>
        </div>
      </div>
    );
  }

  // If logged in  show inquiries
  return (
    <div className="min-h-screen bg-gray-50 p-8">
      <h1 className="text-3xl font-bold text-blue-700 mb-6 text-center">
        Inquiry Dashboard
      </h1>

      {inquiries.length === 0 ? (
        <p className="text-center text-gray-600">No inquiries yet.</p>
      ) : (
        <div className="space-y-4 max-w-3xl mx-auto">
          {inquiries.map((inq) => (
            <div
              key={inq.id}
              className="p-4 bg-white shadow rounded-xl border border-gray-200"
            >
              <p>
                <strong>Name:</strong> {inq.name}
              </p>
              <p>
                <strong>Phone:</strong> {inq.phone}
              </p>
              <p>
                <strong>Course:</strong> {inq.course}
              </p>
              <p>
                <strong>Message:</strong> {inq.message || ""}
              </p>
              <p className="text-sm text-gray-500">
                <strong>Submitted:</strong>{" "}
                {new Date(inq.submittedAt).toLocaleString()}
              </p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
