import React, { useEffect, useState } from "react";
import { ref, onValue, remove } from "firebase/database";
import { signOut } from "firebase/auth";
import { db, auth } from "../firebaseConfig"; // make sure you export both in firebaseConfig
import { motion } from "framer-motion";
import { Search, Trash2, LogOut } from "lucide-react";

export default function AdminDashboard() {
  const [inquiries, setInquiries] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");

  // Fetch data from Firebase Realtime Database
  useEffect(() => {
    const inquiriesRef = ref(db, "inquiries");
    const off = onValue(inquiriesRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        const formatted = Object.entries(data).map(([id, val]) => ({
          id,
          ...val,
        }));
        setInquiries(formatted.reverse()); // latest first
      } else {
        setInquiries([]);
      }
    });

    // onValue returns the unsubscribe function in v10+
    return () => off();
  }, []);

  const handleDelete = async (id) => {
    if (window.confirm("Are you sure you want to delete this inquiry?")) {
      await remove(ref(db, `inquiries/${id}`));
    }
  };

  const handleLogout = async () => {
    try {
      await signOut(auth);
      window.location.href = "/admin-login";
    } catch (e) {
      console.error(e);
      alert("Failed to logout. Try again.");
    }
  };

  // Filtered list (null-safe)
  const term = searchTerm.toLowerCase();
  const filtered = inquiries.filter((item) => {
    const name = (item.name || "").toLowerCase();
    const course = (item.course || "").toLowerCase();
    const email = (item.email || "").toLowerCase();
    return name.includes(term) || course.includes(term) || email.includes(term);
  });

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-100 p-6">
      {/* Header */}
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        className="flex flex-col sm:flex-row justify-between items-center mb-6 gap-3"
      >
        <h1 className="text-3xl font-extrabold text-blue-700"> Admin Dashboard</h1>
        <div className="flex items-center gap-3">
          <p className="text-gray-600 text-sm">
            {new Date().toLocaleString()}
          </p>
          <button
            onClick={handleLogout}
            className="ml-2 flex items-center gap-1 bg-red-500 text-white px-3 py-1 rounded-lg hover:bg-red-600"
          >
            <LogOut size={16} /> Logout
          </button>
        </div>
      </motion.div>

      {/* Search bar */}
      <div className="relative mb-6 max-w-md mx-auto">
        <Search className="absolute left-3 top-3 text-gray-400" size={20} />
        <input
          type="text"
          placeholder="Search by name, email, or course..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
        />
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <motion.table
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="min-w-full bg-white border border-gray-200 rounded-xl shadow-md overflow-hidden"
        >
          <thead className="bg-blue-600 text-white">
            <tr>
              <th className="py-3 px-4 text-left text-sm font-semibold">Name</th>
              <th className="py-3 px-4 text-left text-sm font-semibold">Email</th>
              <th className="py-3 px-4 text-left text-sm font-semibold">Phone</th>
              <th className="py-3 px-4 text-left text-sm font-semibold">Course</th>
              <th className="py-3 px-4 text-left text-sm font-semibold">Date</th>
              <th className="py-3 px-4 text-center text-sm font-semibold">Action</th>
            </tr>
          </thead>

          <tbody>
            {filtered.length > 0 ? (
              filtered.map((inq, i) => (
                <motion.tr
                  key={inq.id}
                  className={`border-t hover:bg-blue-50 transition-all ${
                    i % 2 === 0 ? "bg-white" : "bg-slate-50"
                  }`}
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ delay: i * 0.04 }}
                >
                  <td className="py-3 px-4 text-gray-800 font-medium">
                    {inq.name || ""}
                  </td>
                  <td className="py-3 px-4 text-gray-700">{inq.email || ""}</td>
                  <td className="py-3 px-4 text-gray-700">{inq.phone || ""}</td>
                  <td className="py-3 px-4 text-gray-700 font-semibold">
                    {inq.course || ""}
                  </td>
                  <td className="py-3 px-4 text-gray-600 text-sm">
                    {inq.submittedAt || ""}
                  </td>
                  <td className="py-3 px-4 text-center">
                    <button
                      onClick={() => handleDelete(inq.id)}
                      className="p-2 bg-red-100 hover:bg-red-200 text-red-600 rounded-lg"
                      title="Delete Inquiry"
                    >
                      <Trash2 size={18} />
                    </button>
                  </td>
                </motion.tr>
              ))
            ) : (
              <tr>
                <td colSpan="6" className="text-center py-6 text-gray-500 italic">
                  No inquiries found.
                </td>
              </tr>
            )}
          </tbody>
        </motion.table>
      </div>

      {/* Footer */}
      <footer className="mt-10 text-center text-gray-500 text-sm">
         {new Date().getFullYear()} IT Professional Lab | Admin Panel
      </footer>
    </div>
  );
}
