// src/pages/AdminLogin.jsx
import React, { useEffect, useState } from "react";
import { signInWithEmailAndPassword, signOut } from "firebase/auth";
import { auth } from "../firebaseConfig";
import { motion } from "framer-motion";
import { useNavigate, useLocation } from "react-router-dom";

export default function AdminLogin() {
  const [email, setEmail]     = useState("");
  const [pass, setPass]       = useState("");
  const [err, setErr]         = useState("");
  const [loading, setLoading] = useState(false);

  const navigate  = useNavigate();
  const location  = useLocation();
  const redirectTo = location.state?.from || "/admin/dashboard";

  // Force logout whenever the login page opens
  useEffect(() => {
    signOut(auth).catch(() => {});
  }, []);

  const onSubmit = async (e) => {
    e.preventDefault();
    setErr("");
    setLoading(true);
    try {
      await signInWithEmailAndPassword(auth, email.trim(), pass);
      navigate(redirectTo, { replace: true });
    } catch (e) {
      const msg =
        e.code === "auth/invalid-credential" ? "Invalid email or password"
      : e.code === "auth/user-not-found"     ? "User not found"
      : e.code === "auth/wrong-password"     ? "Wrong password"
      : e.code === "auth/too-many-requests"  ? "Too many attempts. Try later"
      : "Login failed";
      setErr(msg);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen grid place-items-center bg-gradient-to-br from-slate-50 to-blue-100 p-6">
      <motion.div
        initial={{ opacity: 0, y: 12 }}
        animate={{ opacity: 1, y: 0 }}
        className="w-full max-w-md rounded-2xl bg-white p-6 shadow-xl border border-slate-200"
      >
        <h1 className="text-2xl font-extrabold text-blue-700">Admin Login</h1>
        <p className="text-slate-600 text-sm mt-1">Sign in to access the dashboard</p>

        <form onSubmit={onSubmit} className="mt-5 space-y-4">
          <div>
            <label className="text-sm font-medium">Email</label>
            <input
              type="email"
              className="mt-1 w-full rounded-lg border px-3 py-2 outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="admin@example.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              autoFocus
            />
          </div>
          <div>
            <label className="text-sm font-medium">Password</label>
            <input
              type="password"
              className="mt-1 w-full rounded-lg border px-3 py-2 outline-none focus:ring-2 focus:ring-blue-500"
              placeholder=""
              value={pass}
              onChange={(e) => setPass(e.target.value)}
              required
            />
          </div>

          {err && <p className="text-red-600 text-sm">{err}</p>}

          <button
            type="submit"
            disabled={loading}
            className="w-full rounded-lg bg-[#DC2626] text-white font-semibold py-2 hover:brightness-110 disabled:opacity-50"
          >
            {loading ? "Signing in" : "Sign In"}
          </button>
        </form>
      </motion.div>
    </div>
  );
}
